/* eslint-disable */

<template>
  <f7-page>
    <div class="navbar">
      <div class="navbar-inner">
        <div class="nav-left" @click="$f7router.back()">
          <f7-icon ion="ios-arrow-round-back" size="38" />
        </div>
        <div class="title">Table Denis Club</div>
      </div>
    </div>
    <f7-block-title class="text-align-left">Login Credentials</f7-block-title>
    <f7-list inset class="text-align-left">
      <f7-list-item>
        <f7-label>Email</f7-label>
        <f7-input :value="form.email" type="email" placeholder="Your email" required validate error-message="Please input a correct email" @input="form.email = $event.target.value" />
      </f7-list-item>
      <f7-list-item>
        <f7-label>Password</f7-label>
        <f7-input :value="form.password" type="password" required validate pattern=".{8,}" error-message="Must be at least 8 digits" placeholder="Your password" @input="form.password = $event.target.value" />
      </f7-list-item>
      <f7-list-item>
        <f7-label>Confirm Password</f7-label>
        <f7-input :value="form.confirmPassword" type="password" required validate pattern=".{8,}" error-message="Must be at least 8 digits" placeholder="Confirm your password" @input="form.confirmPassword = $event.target.value" />
      </f7-list-item>
    </f7-list>
    <f7-block-title class="text-align-left">Profile</f7-block-title>
    <f7-list inset class="text-align-left">
      <f7-list-item>
        <f7-label>Full Name</f7-label>
        <f7-input :value="form.fullname" required validate type="text" placeholder="Your real full name is required" @input="form.fullname = $event.target.value" />
      </f7-list-item>
      <f7-list-item>
        <f7-label>Emoji</f7-label>
        <f7-input :value="form.emoji" required type="text" placeholder="Pick a emoji to represent yourself" @input="form.emoji = $event.target.value" />
      </f7-list-item>
    </f7-list>
    <div v-if="error" class="error-message">{{error}}</div>
    <div class="actions">
      <f7-button color="white" text-color="black" fill @click="signUp">Sign Up</f7-button>
    </div>
  </f7-page>
</template>
<script>
export default {
  name: 'SignUp',
  data() {
    return {
      form: {
        email: '',
        password: '',
        confirmPassword: '',
        fullname: '',
        emoji: '',
      },
      error: '',
    }
  },
  methods: {
    signUp() {
      // TODO: front-end validation
      this.$f7.dialog.preloader('Signing up...')
      this.$fns
        .httpsCallable('signUp')(this.form)
        .then(({ data: { fullname } }) => {
          this.$f7.dialog.close()
          const notif = this.$f7.notification.create({
            icon: '🏓',
            title: 'Table Denis Club',
            titleRightText: 'now',
            subtitle: 'Profile Created!',
            text: `🎉Welcome, ${fullname}. Sign in now.`,
            closeTimeout: 3000,
            closeOnClick: true,
          })
          notif.open()
          this.$f7router.back()
        })
        .catch((error) => {
          this.$f7.dialog.close()
          this.error = error.message
        })
    },
  },
}
</script>
<style scoped>
.error-message {
  margin: 0 16px;
  color: #ff3b30;
  text-align: left;
  font-size: 12px;
}
.actions {
  display: flex;
  flex-direction: column;
  margin: 16px;
  padding: 16px 0;
}
.nav-left {
  position: absolute;
  left: 16px;
  z-index: 16;
  height: 100%;
}
</style>
